'use client';

import React from 'react';
import { useRouter } from 'next/navigation';
import { IProduct } from '../../../app/hooks/use-choose-pizza';
import {Dialog, DialogContent} from '../../ui';


interface Props {
    product: IProduct;
}

export const ChooseProductModal: React.FC<Props> = ({ product }) => {
    const router = useRouter();
    const isPizzaForm = Boolean(product.items[0].pizzaType);

    const onCloseModal = () => {
        router.back();
    };

    return (
        <Dialog open={Boolean(product)} onOpenChange={() => onCloseModal()}>
            <DialogContent className="p-0 w-[1060px] max-w-[1060px] min-h-[500px] bg-white overflow-hidden">
                {isPizzaForm ? console.log('PizzaForm') : console.log('ProductForm')}
            </DialogContent>
        </Dialog>
    );
};
